.container {
  max-width: 124rem;
  padding-left: 2rem;
  padding-right: 2rem;
  margin: 0 auto;
}

header {
  background-color: #004b93;
  height: 13.5rem;
  padding-left: 8rem;
  padding-right: 8rem;
}
header .container {
  max-width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.visual {
  background-color: #66a7df;
}
.visual__img {
  background: url("../img/visual.png") no-repeat 50% 50%;
  background-size: cover;
  height: 80rem;
  max-width: 192rem;
  margin: 0 auto;
}

nav {
  background-color: #f3f0ee;
}
nav .container {
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-columns: repeat(3, 1fr);
  padding: 3rem 0;
}
nav .container button {
  color: #278fcf;
  transition: all 0.5s;
  will-change: auto;
  font-size: #2990d0;
  font-size: 2.9rem;
}
nav .container button.is-active {
  color: #003b87;
  font-weight: 700;
  font-size: 3.4rem;
}
nav .container button:nth-child(2) {
  border-left: 1px solid #c0d8e6;
  border-right: 1px solid #c0d8e6;
}

.content__title {
  display: flex;
  justify-content: center;
}
.content__title span {
  display: inline-block;
  position: relative;
  color: #004e9d;
  font-family: "NanumSquareNeoExtraBold";
  font-size: 6.5rem;
  padding-top: 4rem;
  padding-bottom: 3rem;
}
.content__title span em {
  font-style: normal;
  color: #f6ac19;
}
.content__title span::after, .content__title span::before {
  content: "";
  display: block;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
.content__title span::after {
  background-color: #004e9d;
  height: 0.7rem;
  width: 10rem;
  top: 0;
  border-radius: 2rem;
}
.content__title span::before {
  bottom: 0;
}

.page1 {
  padding-top: 22rem;
}
.page1__title {
  text-align: center;
  padding-bottom: 8.5rem;
}
.page1__video {
  padding-bottom: 56.25%;
  position: relative;
  margin-bottom: 12rem;
}
.page1__video iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

footer {
  background-color: #004e9d;
  padding: 6.5rem 0;
}
@media (max-width: 768px) {
  footer {
    display: none;
  }
}